import React, { useState, useEffect } from 'react';
import { BdsSelect } from 'blip-ds/dist/blip-ds-react';
import Select from '../Select/index'
import getMessageTemplate from '../../services/getMessageTemplate'
import guid from '../../utils/guid';
import { showToast } from "../../api/commomService";
import getNameSpaceWhatsApp from '../../services/getNameSpaceWhatsApp';
import getStateBot from '../../services/getStateBot';
import getTokenTray from '../../services/getTokenTray';
import getStatuses from '../../services/getOrderStatudes';
import { consumer_key, consumer_secret } from '../../constants/application-actions';
import setServiceTray from '../../services/setServiceTray';

const SendNotification = ({ shortName, accessKey1, email }) => {


  const [config, setConfig] = useState({ value: "No message selected", config: "" });
  const [data, setData] = useState([]);
  const [dataSubBot, setDataSubBot] = useState([]);
  const [dataLanguage, Language] = useState([{ label: 'Português', value: 'pt_BR' }, { label: 'Inglês', value: 'en_US' }]);
  const [message, setMessage] = useState([])
  const [template, setTemplate] = useState('');
  const [stateId, setStateId] = useState('');
  const [flowId, setFlowId] = useState('');
  const [filterLanguage, setFilterLanguage] = useState('');
  const [inputList, setInputList] = useState([{ state: "", template: "" }]);
  const [loading, setLoading] = useState(false);
  const [phone, setPhone] = useState('');
  const [templateName, setTemplateName] = useState('Select a template');
  const [language, setLanguage] = useState('Select a language');
  const [subBots, setSubBots] = useState(JSON.parse(localStorage.getItem('details')));
  const [templateSubBot, setTemplateSubBot] = useState('Select a template');
  const [identity, setIdentity] = useState('');
  const [dataFlowId, setDataFlowId] = useState([{ label: "No message selected", value: "dfdd" }]);
  const [templateSubBotFlowId, setTemplateSubBotFlowId] = useState('Select a state');
  const [inputValueList, setInputValueList] = useState("");
  const [showStatus, setShowStatus] = useState(false);
  const [statusTray, setStatusTray] = useState(false);


  const fnAuxTemplates = async () => {

    const arrayTemplates = [];

    const response = await getMessageTemplate(guid(), localStorage.getItem('info'));

    const templates = response.data.resource.data.filter((t) => t.name && t.language === filterLanguage && t.name.split('_')[0] !== 'sample' && t.status === 'APPROVED');

    templates.map((template) => {
      arrayTemplates.push({ id: template.name, label: template.name, value: template.name, selected: false })
    });


    setData(arrayTemplates);
    setMessage(templates);


  }

  const fnAuxStatusTray = async () => {

    const url = localStorage.getItem('plugin-tray-key2');
    const code = localStorage.getItem('plugin-tray-key');

    const arrayStatusTray = []

    const { data: { access_token } } = await getTokenTray(url, consumer_key, consumer_secret, code);

    const { data } = await getStatuses(url, access_token);

    data.OrderStatuses.map((e) => {

      arrayStatusTray.push({ id: e.OrderStatus.id, label: e.OrderStatus.status, value: e.OrderStatus.id, selected: false })

    })

    setStatusTray(arrayStatusTray);

  }


  const fnAuxSubBots = () => {

    const arraySubBots = [];
    subBots.map((bots) => { arraySubBots.push({ id: bots.longName, label: bots.longName, value: bots.longName, selected: false }); })
    setDataSubBot(arraySubBots);

  }

  useEffect(() => {

    const url = localStorage.getItem('plugin-tray-key2');
    const code = localStorage.getItem('plugin-tray-key');

    if (code !== '' && url !== '') {

      fnAuxStatusTray()
    }

    fnAuxTemplates()

  }, [filterLanguage]);



  useEffect(() => {
  
    fnAuxSubBots()

  }, []);


  const handleInputChange = (e, index) => {

    const list = [...inputList];

    list[index].template = e;
    setInputList(list);
  };
  const handleInputChangeStatus = (e, index) => {

    const list = [...inputList];

    list[index].state = e;
    setInputList(list);
  };


  const handleRemoveClick = index => {

    const list = [...inputList];
    list.splice(index, 1);
    setInputList(list);
  };


  const handleAddClick = () => {

    if (inputList.length >= 5) {
      showMessage('Não é possivel incluir mais status!', 'warning', 'Notice!');
      return
    }
    setInputList([...inputList, {
      state: "",
      template: ""
    }
    ]);
  };


  const handleSendMessage = async () => {
    setLoading(true);


    if (!localStorage.getItem("plugin-tray-key")) {
      showMessage('O campo Code precisa ser preenchido!', 'danger', 'Notice!');
      redirectFirstItem()
      return
    }

    if (!localStorage.getItem("plugin-tray-key2")) {
      showMessage('O campo URL precisa ser preenchido!', 'danger', 'Notice!');
      redirectFirstItem()
      return
    }

    if (!localStorage.getItem("plugin-tray-key3")) {
      showMessage('O campo Id da loja precisa ser preenchido!', 'danger', 'Notice!');
      redirectFirstItem()
      return;
    }

    if (!filterLanguage) {
      showMessage('O campo idioma precisa ser preenchido!', 'warning');
      return
    }
    if (!identity) {
      showMessage('O campo Chatbot precisa ser preenchido!', 'warning');
      return
    }
    if (!stateId) {
      showMessage('O campo Bloco precisa ser preenchido!', 'warning');
      return
    }

    const resultNameSpace = await getNameSpaceWhatsApp(guid(), localStorage.getItem('info'));

    let app = resultNameSpace.data.resource.name

    const payload = {
      flowId: flowId,
      masterState: identity,
      stateId: stateId,
      languageCode: filterLanguage,
      namespace: resultNameSpace.data.resource['message_template_namespace'],
      trackOrigin: true,
      checkAttendance: true,
      senderEmail: email,
      messageTemplate: inputList,
      accessKey1,
      shortName,
      url: localStorage.getItem("plugin-tray-key2"),
      code: localStorage.getItem("plugin-tray-key"),
      storeId: localStorage.getItem("plugin-tray-key3"),
      company: app,
      apiKey: 'Key ' + btoa(`${shortName}:` + atob(accessKey1)),  
      consumer_key,
      consumer_secret
    }

    const {status} = await setServiceTray(payload);

    if (status === 409) {

      showMessage('Essa configuração já existe', 'warning');
      return
    }

    showMessage('Configuração salva com sucesso', 'success');

    setTimeout(() => {
      window.location.reload();
    }, 3000);
  }

  const redirectFirstItem = () => {

    setTimeout(() => {
      document.querySelector("a").click()
    }, 2000);

  }

  const showMessage = (msg, type) => {

    setTimeout(() => {

      showToast({
        type: type,
        message: msg
      })
      setLoading(false);

    }, 900);

  }

  const handleSetEventLanguage = (event) => {

    setFilterLanguage(event);
    setLanguage(event);

  }


  const handleSetEventSubBot = async (event) => {

    const nameSubBot = event
    setTemplateSubBot(nameSubBot);

    const { identifier, accessKey, identity } = subBots.filter((e) => e.longName === nameSubBot)[0];
    const KEY = 'Key ' + btoa(`${identifier}:` + atob(accessKey));
    const resultHttp = await getStateBot(KEY, shortName, accessKey);
    const data = resultHttp.data.statesInformation;

    let arrayStates = [];

    for (const iterator of data) {

      arrayStates.push({ id: iterator.name, label: iterator.name, value: iterator.id, selected: false })

    }

    setDataFlowId(arrayStates);
    setFlowId(resultHttp.data.flowId);
    setIdentity(identity);
  }
  const handleSetEventSubBotFlowId = async (event) => {

    const url = localStorage.getItem('plugin-tray-key2');
    const code = localStorage.getItem('plugin-tray-key');

    if (code !== '' && url !== '') {
      setTemplateSubBotFlowId(event);
      setStateId(event);
      setShowStatus(true)
    } else {

      showMessage('Você precisa fazer a configuração da tray', 'warning');
      redirectFirstItem();
    }

  }
  return (

    <>
      <div className="container w-100">
        <div className="row">


          <div className="col-3">
            <label>Idoma: </label>
            <Select
              icon="search"
              name="template"
              label="Selecione um idioma"
              placeholder="Selecione"
              multiple={false}
              searchable={true}
              options={dataLanguage}
              selected={dataLanguage}
              onChange={($event) => handleSetEventLanguage($event)}
            />
          </div>
          <div className="col-3">
            <div className="col-3">
              <label>Chatbot:</label>
            </div>
            <Select
              icon="search"
              name="template"
              label="Selecione um Chatbot"
              placeholder="Selecione"
              multiple={false}
              searchable={true}
              options={dataSubBot}
              selected={dataSubBot}
              onChange={($event) => handleSetEventSubBot($event)}
            />
          </div>
          <div className="col-6">
            <div className="col-6">
              <label>Bloco:</label>
            </div>
            <Select
              icon="search"
              name="template"
              label="selecione um Bloco"
              placeholder="Selecione"
              multiple={false}
              searchable={true}
              options={dataFlowId}
              selected={dataFlowId}
              onChange={($event) => handleSetEventSubBotFlowId($event)}
            />
          </div>
          <hr />
          {showStatus && <div className="container w-100">
            <hr />

            <div className="row">
              <div className="col-12">
                <label>Status Tray:</label>
              </div>
              <div className="col-12">

              </div>
            </div>

            {inputList.map((x, i) => {
              return (
                <div className="mt-2 mb-2" key={i.toString()}>
                  <div class="row">
                    <div class="col-4">
                      <label>Status- {i + 1}:</label>
                      <Select
                        icon="filter"
                        name="state"
                        label="Selecione um status"
                        placeholder="Selecione"
                        multiple={false}
                        searchable={true}
                        options={statusTray}
                        selected={statusTray}
                        onChange={($event) => handleInputChangeStatus($event, i)}
                      />
                    </div>
                    <div class="col-6">
                      <label>Template - {i + 1}:</label>
                      <Select
                        icon="filter"
                        name="template"
                        label="Selecione um modelo de mensagem"
                        placeholder="Selecione"
                        multiple={false}
                        searchable={true}
                        options={data}
                        selected={data}
                        onChange={($event) => handleInputChange($event, i)}
                      />
                    </div>
                    <div class="col-1">
                      <bds-icon name="trash" color="red" style={{ marginTop: '75%', cursor: 'pointer' }} onClick={() => handleRemoveClick(i)} role="img" class="bds-icon bds-icon__size--medium hydrated" aria-label="trash" theme="outline"></bds-icon>

                    </div>
                  </div>
                  <hr />
                </div>
              );
            })}


            <div style={{ display: 'flex', justifyContent: 'space-between' }}>

              
              {inputList.length < 5 && <bds-button icon="plus"   style={{ width: '45%' }} size="short" onClick={handleAddClick} >Adicionar Status</bds-button>}

              {inputList.length > 0 && <bds-button icon="save-disk"  size="short" onClick={handleSendMessage} style={{ width: inputList.length < 5 ? '45%' : '100%' }} >Salvar configuração</bds-button>}

            </div>



          </div>}

        </div>
      </div>
    </>

  )

};

export default SendNotification;